}

.form-add{
  color: #F0F5F7;
  text-align: center;
}

label {
    padding: 0;
    font-size: 17px;
    color: #202124;
    font-weight: 500;
    margin: 27px 0 10px;
    display: block;
}

select, textarea, input[type="text"] {
    margin: 0 !important;
    border-radius: 8px;
    border: 1px solid #F0F5F7;
    padding: 5px 20px;
    font-size: 15px;
    background: #F0F5F7;
    width: 100% !important;
    color: #696969;
    transition: all 0.3s ease-in-out 0s;
    box-shadow: none;
    height: 60px;
}

.form-control{
    display: block;
    width: 100%;
    padding: .375rem .75rem;
    font-size: 1rem;
    font-weight: 400;
    line-height: 1.5;
    color: var(--bs-body-color);
    background-color: var(--bs-form-control-bg);
    background-clip: padding-box;
    border: var(--bs-border-width) solid var(--bs-border-color);
    -webkit-AddProjectearance: none;
    -moz-AddProjectearance: none;

    border-radius: .375rem;
    transition: border-color .15s ease-in-out,box-shadow .15s ease-in-out;
}


.rdw-editor-main {
    background-color: #f5f6f9;
    border: 1px solid#eee;
    margin-bottom: 17px;
    padding: 7px 17px 7px 17px;
    line-height: 27px;
  }
  .public-DraftEditor-content {
    min-height: 300px;
  }
  .rdw-dropdown-optionwrAddProjecter {
    width: 100%!important;
