  input, button, select, textarea {
    font-family: inherit;
    font-size: inherit;
    line-height: inherit;
  }
  label {
    padding: 0;
    font-size: 17px;
    color: #ffffff;
    font-weight: 500;
    margin: 27px 0 10px;
    display: block;
  }
  .btn {
    display: inline-block;
    margin-bottom: 0;
    font-weight: 400;
    font-family: 'Inter', sans-serif;
    text-align: center;
    vertical-align: middle;
    cursor: pointer;
    background-image: none;
    border: 1px solid transparent;
    white-space: nowrap;
    letter-spacing: 0;
    padding: 9px 30px;
    font-size: 15px;
    line-height: 2;
    border-radius: 8px;
    -webkit-user-select: none;
    user-select: none;
    transition: all 0.3s ease-in-out 0s;
  }
  .btn, button {
    outline: none !important;
  }
  .btn__theme {
    color: #fff;
    background-color: #1967d2;
    border-color: #1967d2;
  }
